import "./PassengerForm.css";

import { useState } from "react";

const PassengerForm = () => {
  const [formData, setFormData] = useState({
    type: "", // =teen, =adult,   =child
    lastName: "",
    firstName: "",
    middleName: "",
    gender: "", // M  
    birthDate: "",
    mobility: false, //  
    documentType: "",
    documentNumber: "",
  });

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: type === "checkbox" ? checked : value,
    }));
  };

  const isFormValid = () => {
    //   
    //      
    return Object.values(formData).every((val) => val);
  };

  return (
    <div
      className={`passenger-form ${
        isFormValid() ? "passenger-form--valid" : "passenger-form--invalid"
      }`}
    >
      {/*     */}
      <div className="passenger-form__row">
        <select name="type" value={formData.type} onChange={handleChange}>
          <option value=""> </option>
          <option value="adult"></option>
          <option value="child"></option>
        </select>
      </div>

      {/*    */}
      <div className="passenger-form__row">
        <input
          name="lastName"
          placeholder=""
          value={formData.lastName}
          onChange={handleChange}
        />
        <input
          name="firstName"
          placeholder=""
          value={formData.firstName}
          onChange={handleChange}
        />
        <input
          name="middleName"
          placeholder=""
          value={formData.middleName}
          onChange={handleChange}
        />
      </div>

      {/*        */}
      <div className="passenger-form__row">
        <button
          type="button"
          className={`gender-button ${
            formData.gender === "M" ? "gender-button--selected" : ""
          }`}
          onClick={() => setFormData({ ...formData, gender: "M" })}
        >
          
        </button>
        <button
          type="button"
          className={`gender-button ${
            formData.gender === "" ? "gender-button--selected" : ""
          }`}
          onClick={() => setFormData({ ...formData, gender: "" })}
        >
          
        </button>
        <input
          type="date"
          name="birthDate"
          value={formData.birthDate}
          onChange={handleChange}
        />
      </div>

      {/*    */}
      <div className="passenger-form__row">
        <label>
          <input
            type="checkbox"
            name="mobility"
            checked={formData.mobility}
            onChange={handleChange}
          />
           
        </label>
      </div>

      <hr />

      {/*       */}
      <div className="passenger-form__row">
        <div className="passenger-form__row">
          <select
            name="documentType"
            value={formData.documentType}
            onChange={handleChange}
          >
            <option value="">  </option>
            <option value="passport"></option>
            <option value="birthCertificate">  </option>
          </select>
          <input
            name="documentNumber"
            placeholder=""
            value={formData.documentNumber}
            onChange={handleChange}
          />
        </div>
        <input
          name="documentNumber"
          placeholder=""
          value={formData.documentNumber}
          onChange={handleChange}
        />
      </div>

      {/*     */}
      <div className="passenger-form__footer">
        <div
          className={`status-indicator ${
            isFormValid()
              ? "status-indicator--valid"
              : "status-indicator--invalid"
          }`}
        >
          {isFormValid() ? "" : " "}
        </div>
        <button> </button>
      </div>
    </div>
  );
};

export default PassengerForm;
